.special {
	background-color: $special_bg;
	padding: 24px 0;
	@include sm {
		background-color: transparent;
		padding: 5px 0; }
	&__text {
		text-align: center;
		font-weight: 400; }
	&__bold-light {
		font-weight: 800;
		color: $card-price-color;
		font-family: $corp; }
	&__bold-dark {
		color: $title_dark; } }
